}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

h1, h2, h3, p {
  margin: 0;
}

body {
  color: var(--primary-text-color);
  background-color: var(--primare-bg-color);
  letter-spacing: .04em;
  font-family: DM Sans, sans-serif;
}

a {
  cursor: url("cursor-icecream.87810c28.png"), auto;
  text-decoration: none;
  display: block;
}

img {
  height: auto;
  max-width: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  cursor: url("cursor-icecream.87810c28.png"), auto;
  border: 0;
  padding: 0;
}

.button {
  display: block;
}

.button:after {
  content: "";
  height: 100%;
  width: 100%;
  z-index: -2;
  transition: all 350ms var(--timing-function);
  opacity: .3;
  background-color: var(--primare-bg-color);
  border-radius: 250px;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.button:hover:after {
  opacity: 0;
  background-color: var(--accent-color);
  transform: scaleX(1.8)scaleY(1.9);
}

.button--white {
  transition: all 250ms var(--timing-function);
  position: relative;
}

.button--white:hover, .button--white:focus {
  box-shadow: inset 0 0 14px var(--accent-color);
  outline-offset: 15px;
  border: 1px solid rgba(212, 20, 67, .5);
  outline-color: rgba(255, 255, 255, 0);
  transform: scale(1.15);
}

.button--pink {
  box-shadow: inset 0 0 14px var(--accent-color);
  transition: all 250ms var(--timing-function);
  position: relative;
  transform: scale(1);
}

.button--pink:hover, .button--pink:focus {
  border: 1px solid var(--primare-bg-color);
  box-shadow: inset 0 0 14px var(--primare-bg-color);
  outline-color: var(--primare-bg-color);
  outline-offset: 15px;
  background-color: rgba(212, 20, 67, .9);
  transform: scale(1.11);
}

