.advantage__number {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--accent-color);
  font-family: Titan One;
  font-size: 36px;
  font-weight: 400;
  line-height: 1.1389;
}

@media screen and (min-width: 768px) {
  .advantage__number {
    font-size: 48px;
    line-height: 1.1458;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__number {
    font-size: 58px;
    line-height: 1.1379;
  }
}

.advantage__description {
  color: var(--accent-text-color);
  margin-top: 25px;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.7;
}

@media screen and (min-width: 768px) {
  .advantage__description {
    font-size: 14px;
    line-height: 1.74;
  }
}

@media screen and (min-width: 1200px) {
  .advantage__description {
    letter-spacing: .02em;
    font-size: 16px;
    line-height: 1.76;
  }
}

.gallery {
  background-image: linear-gradient(180deg, var(--secondary-bg-color) 42%, var(--primare-bg-color) 0);
  padding-top: 60px;
  padding-bottom: 30px;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .gallery {
    padding-top: 64px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery {
    padding-top: 120px;
  }
}

.gallery__list {
  justify-content: center;
  align-items: center;
  gap: 2.53px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .gallery__list {
    gap: 6px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery__list {
    gap: 8px;
  }
}

.gallery__item:not(:last-child) {
  margin-bottom: 1.89px;
}

@media screen and (min-width: 768px) {
  .gallery__item:not(:last-child) {
    margin-bottom: 6.05px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery__item:not(:last-child) {
    margin-bottom: 8px;
  }
}

.gallery__item:hover {
  z-index: 10;
  position: relative;
}

.gallery__image {
  width: 100%;
  border: rgba(0, 0, 0, 0);
  border-radius: 8px;
  transition: transform 1s;
}

.gallery__image:hover {
